.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px gray;
    margin: 20px;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
    align-items: center;
    min-width: 40%;
}

.img{
    border-radius: 60%;
}